ul {margin:0;padding:0;list-style-type:none;}
body {
  color: #34538b;
  margin-top: 0;
}
a{color:#34538b;text-decoration:none;}
img,video, object, embed{border:0;max-width:100%;}
#wrap {
  margin: 0 auto;
  padding: 0 5px;
}
#logo {
  font-size: 20px;
  color: red;
  font-weight: bold;
}
#sidebar {
  width: 38%;
  float: right;
  // padding: 0 5px;
}
#content {
  width:60%;
  float: left;
  vertical-align:top;
  // padding: 0 5px;
}
#main {
  vertical-align:top;
}
@media (max-width: 760px)
{
  #sidebar {
    width:100%;
    margin-top: 10px;
    border-top: 2px solid #337ab7;
    padding-top: 10px;
  }
  #content {
    width:100%;
  }
}
#footer {
  clear: both;
  border-top: 2px solid #337ab7;
  font-size: 14px;
  margin-top: 5px;
  padding-top: 5px;
  text-align: center;
}
#header {
  padding:10px 4px;
}
#banner {
  padding: 0 5px 10px;
  text-align: justify;
}
#banner span {
  display: inline-block;
}
#banner:after {
  content: "";
  display: inline-block;
  overflow: hidden;
  width: 100%;
}
#sub {
  font-size: 12px;
}
#menu {
  font-size: 14px;
  border-top: 2px solid #337ab7;
  border-bottom: 2px solid #337ab7;
}
#menu li,.paging li {
  display: inline;
}
#menu ul {
  padding: 2px 0;
}
#menu li {
  margin-right: 10px;
}
#menu a {
  display: inline-block;
  padding: 0 5px;
  height: 30px;
  line-height: 30px;
}
#menu .on {
  background-color: #337ab7;
  color: white;
}
#content h3 {
  margin: 5px 0;
}
#content h2 {
  margin-top: 0;
}
#content .item {
  margin-bottom: 20px;
}
#content .item .desc {
  margin: 5px 0;
  font-size: 16px;
}
#content .item .detail {
  margin: 5px 0 10px;
  font-size: 14px;
}
#content .highlight {
  background-color: #303030;
  color: whitesmoke;
  overflow-x:auto;
}
#sidebar .me-info {
  font-size: 14px;
}
#sidebar .me-info img {
  float: left;
  width: 70px;
  height: 70px;
}
#sidebar .me-info .detail {
  // height: 70px;
  // line-height: 21px;
  // font-size: 12px;
  padding-left:75px;
  // text-align: justify;
  min-height: 70px;
}
#sidebar .me-info .detail p {
  margin: 0;
}
#sidebar .box {
  padding-bottom: 5px;
}

#sidebar .box .title {
  font-size: 16px;
  font-weight: bold;
  margin: 5px 0;
  padding-top: 5px;
  // padding-left: 10px;
  border-top: 2px solid #337ab7;
  position: relative;
}

.tag a {
    padding: 0 5px;
    border-radius: 2px;
    margin-right: 5px;
    border: 2px solid #ccc;
    // margin-top: 5px;
    line-height: 30px;
    font-size: 14px;
}

.tag a { color:#34538b; background-color: white; transition:0.5s; }
.tag a:hover { color:white; background-color: #34538b;}
.tag .on {
  color:white; 
  background-color: #34538b;
}
